import { Injectable } from '@angular/core';
import { IViewActionService } from '@helix/platform/view/api';
import { EMPTY, Observable } from 'rxjs';
import { ICloseViewActionParams } from './close-view-action-params.interface';
import { tap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RxCloseViewActionService implements IViewActionService<ICloseViewActionParams, never> {
  execute(params: ICloseViewActionParams): Observable<never> {
    return params.actAsCancel
      ? params.viewApi.cancel()
      : EMPTY.pipe(
          tap({
            complete() {
              params.viewApi.close();
            }
          })
        );
  }
}
